@import url(https://at.alicdn.com/t/font_2565439_qwroexj64q.css);
* {
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.App {
    text-align: center;
    box-sizing: border-box;
}

ul {
    list-style: none;
}


/* 滚动条配置 */

::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: 6px !important;
    /*高宽分别对应横竖滚动条的尺寸*/
    height: 6px !important;
    background: #ffffff !important;
    cursor: pointer;
}

::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    border-radius: 5px !important;
    box-shadow: inset 0 0 5px rgba(240, 240, 240, 0.5) !important;
    background: rgba(63, 98, 131, 0.8) !important;
    cursor: pointer !important;
}

::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    box-shadow: inset 0 0 5px rgba(240, 240, 240, 0.5) !important;
    border-radius: 0 !important;
    background: rgba(240, 240, 240, 0.5) !important;
    cursor: pointer !important;
}

html {
    font-size: 62.5%;
    background-color: rgb(254, 250, 246);
    color: rgb(35, 35, 32);
}

a {
    text-decoration: none;
}

.nav {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 100;
    background: rgb(254, 250, 246);
}

.nav .nav-list {
    display: flex;
    justify-content: flex-end;
    padding: 0 3rem;
}

.nav .nav-list .nav-item {
    padding: 1rem;
    font-size: 1.6rem;
}

.nav .nav-list .nav-item i {
    margin-right: .3rem;
}

@media (max-width:1200px) {
    html {
        font-size: 60%;
    }
}

@media (max-width:980px) {
    html {
        font-size: 58%;
    }
}

@media (max-width: 460px) {
    html {
        font-size: 55%;
    }
}

.Welcome-section {
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.Welcome-section h1 {
    font-size: 4.2rem;
}

.project-section {
    text-align: center;
    padding: 7rem 1.5rem;
}

@media (max-width: 460px) {
    .project-section-header {
        font-size: 1.5rem;
    }
}

.project-section-header {
    font-size: 3rem;
    max-width: 640px;
    margin: 3rem auto 4rem;
    padding-bottom: 2rem;
    border-bottom: .3rem solid rgb(35, 35, 32);
}

.project-section-content {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: auto;
    max-width: 1200px;
    padding: 0;
}

.project-section-content .project-section-item {
    flex-basis: 38rem;
    /* height: 3rem; */
    margin: .7rem 1rem;
    flex-grow: 0;
    flex-shrink: 1;
    background-image: linear-gradient(to top, #e6e9f0 0%, #eef1f5 100%);
    border-radius: .7rem;
    overflow: hidden;
    cursor: pointer;
    position: relative;
}

.project-section-content .project-section-item a {
    display: block;
}

.project-section-content .project-section-item img {
    height: calc(100% - 6.8rem);
    width: 100%;
    object-fit: cover;
}

.project-item-title {
    font-size: 1.5rem;
    padding: .5rem 0.5rem;
    font-weight: bold;
}


/* footer */

.footer-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    height: 20vh;
}

.footer-section h2 {
    width: 50%;
    padding: 15rem;
    font-size: 4.2rem;
}

.footer-section center {
    flex-basis: 3rem;
    flex-shrink: 0;
}

@media (max-width: 460px) {
    .footer-section h2 {
        width: 100%;
        padding: 1rem;
        font-size: 1.2rem;
    }
    .footer-section center {
        flex-basis: 2rem;
        font-size: 1rem;
        flex-shrink: 0;
    }
}


/* 针对项目列表 */

.project-section-item .li-hover {
    position: absolute;
    width: 100%;
    height: 80%;
    background-color: rgba(240, 240, 240, 0.5);
    z-index: 12;
    left: 0;
    top: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: all .5s ease-out;
}

.project-section-item .li-hover i {
    font-size: 2.5rem;
    padding: 1rem;
    font-weight: bold;
}

.code {
    color: transparent;
    transition: color 0.3s ease-out;
}

.project-section-item:hover .code {
    color: #ff7f50;
}

.project-section-item:hover .li-hover {
    opacity: 1;
}

.project-section-item .li-hover i:hover {
    background-color: azure;
    border-radius: 50%;
    color: #ff7f50;
}

a:hover {
    color: #ff7f50
}
.resume {
    font-size: 16px;
    font-family: 'Lucida Grande', 'Hiragino Sans GB', 'Hiragino Sans GB W3', 'Helvetica Neue', 'Helvetica', 'PingFang SC', 'Microsoft YaHei', '微软雅黑', 'Arial,sans-serif';
    position: relative;
    width: 980px;
    margin: 50px auto 100px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 0 15px #c0c0c0;
    overflow: hidden;
    transition: all .2s ease-in-out;
    /* opacity: 0; */
    transform: translate3d(0, 50px, 0);
    /* background-color: aquamarine; */
}


/* 头部 */

.resume-hd {
    padding: 30px 50px;
    color: #fff;
    background-color: #00b38a;
    /* overflow: hidden; */
}


/* 伪类清除浮动 */

.resume-hd::after {
    content: '';
    height: 0;
    line-height: 0;
    display: block;
    clear: both;
    visibility: hidden;
}

.resume-hd .title {
    overflow: hidden;
    border-bottom: 2px solid #00a982;
    margin-bottom: 1rem;
}

.resume-hd .title .name-job {
    transform: translateY(3rem);
    float: left;
}

.resume-hd .title .passport {
    float: right;
    padding-right: 3rem;
}


/* 证件照区域 */

.resume-hd .title .passport img {
    width: 10rem;
    border-radius: 2px;
}


/* 名字以及意向职位 */

.resume-hd .title .name-job .name h1 {
    font-size: 2.5em;
}

.resume-hd .title .name-job .name small {
    font-size: 2rem;
}


/* 意向职位 */

.resume-hd .title .name-job .job {
    padding: 1rem 0;
    letter-spacing: .2rem;
}

.resume-hd .info,
.resume-hd .contact {
    width: 50%;
    padding: 2rem 0 1.3rem;
}


/* info */

.resume-hd .info {
    float: left;
}

.resume-hd .info h2 {
    font-size: 1.7rem;
    padding: .5rem;
    font-weight: 400;
}


/* contact联系方式 */

.resume-hd .contact {
    float: right;
}

.resume-hd .contact ul li {
    padding: .2rem 0;
    text-align: right;
    font-size: 1.5rem;
}

.resume-hd .contact li i,
.resume-hd .contact li a {
    color: white;
    margin-left: .5rem;
}


/*  */


/* 主体 */

.resume .resume-bd {
    min-height: 200px;
    padding: 1.2rem 4.3rem 3rem;
    /* overflow: hidden; */
    color: black;
}

.resume .resume-bd .item a {
    color: black;
}

.resume .resume-bd section {
    padding: 10px 18px;
    box-sizing: border-box;
}


/* 简历主体通用头部 */

.section-hd {
    position: relative;
    height: 4rem;
    margin: 10px auto 8px;
    overflow: hidden;
}

.section-hd .section-title-l {
    display: inline-block;
    position: absolute;
    top: 50%;
    width: 35%;
    height: 0;
    left: 10rem;
    border-top: 1px solid #eee;
    vertical-align: middle;
}

.section-hd .section-title-r {
    right: 10rem;
    display: inline-block;
    position: absolute;
    top: 50%;
    width: 35%;
    border-top: 1px solid #eee;
    vertical-align: middle;
}

.section-hd .section-title {
    position: relative;
    z-index: 1;
    width: 24%;
    min-width: 100px;
    margin: 0 auto;
    padding: 1rem 10px;
    border-radius: 32px;
    font-size: 1.5rem;
    text-align: center;
    background-color: #eee;
}


/* 项目经历 */

.resume-bd .project {
    width: 100%;
}

.project .section-bd {
    overflow: hidden;
}

.section-bd .item {
    padding: 1rem 2rem;
}

.section-bd .item .item-hd {
    overflow: hidden;
    padding: 1rem 0;
    width: 100%;
}

.section-bd i.dot {
    width: .5rem;
    height: .5rem;
    display: block;
    border-radius: 100%;
    background-color: #00b38a;
    position: absolute;
    z-index: 10;
    left: 0px;
    top: 1.5rem;
}

.section-bd .item-hd h3 {
    float: left;
    width: 60%;
    font-size: 2.2rem;
    text-align: left;
    position: relative;
    padding-left: 1rem;
}

.section-bd .item-hd .links {
    float: right;
    width: 30%;
    text-align: right;
    transform: translateY(.2rem);
}

.section-bd .item-hd .links a {
    margin-right: 1rem;
    font-weight: 700;
    color: rgba(0, 0, 0, .8);
}

.section-bd .item-bd {
    padding: 1rem .1rem;
    line-height: 2.5rem;
}

.section-bd .item-bd span {
    font-weight: 700;
    margin-left: .2rem;
}


/* 相关技能描述 */

.section-bd .section-list {
    padding: .5rem 1rem;
    line-height: 2.4rem;
}

.section-bd .section-list i.dot {
    top: 1.5rem;
}

.section-bd .section-list li {
    position: relative;
    padding: .6rem 3rem .6rem 1.5rem;
}


/* 校园经历 */

.work .section-bd .item-hd small {
    margin-left: 1rem;
    color: #566270;
}

.work .section-bd .item-hd .time {
    float: right;
}


/* 自我评价 */

.self-review .section-bd p {
    margin-top: 2rem;
    text-indent: 2em;
    line-height: 2.5rem;
}

.resume .resume-bd strong {
    color: #F60;
    font-weight: 500;
}

@media print {
    html {
        background-color: white;
    }
    body {
        padding-top: 0
    }
    .resume {
        margin: 0 auto;
        border-radius: 0;
        box-shadow: none
    }
    .resume-hd {
        padding: 30px 50px;
        color: #fff;
        background-color: #00b38a;
    }
    .resume-bd {
        padding-top: 0;
    }
}
